import React from "react";
import { Col, Row, Container } from "react-materialize";

const NoMatch = () =>
  <Container fluid>
    <Row>
      <Col l={12}>
        
          <h1>404 Page Not Found</h1>
      </Col>
    </Row>
   
  </Container>;

export default NoMatch;


